import React from "react";

// reactstrap components
import {
  Container,
  Row,
  Col
} from "reactstrap";

// core components
import ExamplesNavbar from "components/Navbars/ExamplesNavbar.js";
import LandingPageHeader from "components/Headers/LandingPageHeader.js";
import DefaultFooter from "components/Footers/DefaultFooter.js";

function LandingPage() {

  React.useEffect(() => {
    document.body.classList.add("landing-page");
    document.body.classList.add("sidebar-collapse");
    document.documentElement.classList.remove("nav-open");
    return function cleanup() {
      document.body.classList.remove("landing-page");
      document.body.classList.remove("sidebar-collapse");
    };
  });
  return (
    <>
      <ExamplesNavbar />
      <div className="wrapper">
        <LandingPageHeader />
        <div className="section section-about-us" id={'sobre'}>
          <Container>
            <Row>
              <Col className="ml-auto mr-auto text-center" md="8">
                <h2 className="title" id={'sobre'}>Sobre a iGive</h2>
                <h5 className="description">
                Tem se verificado anualmente um aumento da quantidade de resíduos urbanos em Portugal, sendo que, 385 mil toneladas destes resíduos são têxteis e mobiliários. No entanto, a percentagem de reaproveitamento e reciclagem não tem verificado o mesmo aumento.
                <br/><br/>
Existindo esta elevada taxa de desperdício de bens materiais, associada a um desperdício dos recursos utilizados na sua produção, surge a necessidade de promover a reutilização destes bens e otimização de recursos.
<br/><br/>
A plataforma online iGive surge de forma a minimizar este desperdício de bens e otimizar os recursos, promovendo a interação entre doadores de bens materiais ou tempo, podendo estes ser uma pessoa individual ou uma grande ou pequena superfície (supermercados, restaurantes, hotéis, etc.), e recetores, principalmente os mais carenciados tais como instituições humanitárias, comunidades solidárias, canis, orfanatos, etc.

                </h5>
              </Col>
            </Row>
            <div className="section-story-overview">
              <Row>
                <Col md="6">
                  <div
                    className="image-container image-left"
                    style={{
                      backgroundImage:
                        "url(" + require("assets/img/login.jpg") + ")"
                    }}
                  >
                    <p className="blockquote blockquote-info">
                      "A solidariedade é o sentimento que melhor expressa o respeito pela dignidade humana." <br></br>
                      <br></br>
                      <small>-Franz Kafka</small>
                    </p>
                  </div>
                  <video autoPlay loop
                  className="image-container"
                  src={require("assets/img/igive.webm")}></video>
                  
                </Col>
                <Col md="5">
                  <div
                    className="image-container image-right"
                    style={{
                      backgroundImage:
                        "url(" + require("assets/img/bg1.jpg") + ")"
                    }}
                  ></div>
                  <div className="section" id={'comofunciona'}>
                    <Container>
                      <Row>
                        <Col>
                        <h3>
                    Como funciona a plataforma?
                  </h3>
                  <p>
Para que esta interação entre doadores e instituições carenciadas seja promovida, é necessário que ambas as partes se inscrevam na plataforma iGive.
</p>
<p>
Por um lado, as instituições carenciadas, que preenchem um formulário de acordo com as suas necessidades especificas (no âmbito alimentar, animal, material de escritório, mobiliário, entre outros) e que disponibilizam opcionalmente um funcionário ou voluntário para fazer o serviço de recolha e entrega dos bens doados.
</p>
<p>
Por outro lado, os doadores: nós, de forma individual, ou as pequenas e grandes superfícies.
</p>
<p>
Em termos materiais, sem compromisso e em qualquer altura do dia ou até mesmo do ano, os doadores especificam os bens materiais que pretendem doar de forma a que a instituição sugerida para entrega seja a mais adequada. No entanto, a escolha da instituição pode sempre ser alterada se o doador assim o desejar. Para além disto, o doador tem ainda a opção de entregar os seus bens à devida instituição ou de pedir um funcionário ou voluntário da mesma para que este faça o serviço de entrega e recolha. 
</p>
<p>
Aquele que para além de bens materiais pode doar o seu tempo, pode também inscrever-se na plataforma atuando sob a forma de voluntariado, em situações em que o doador de bens materiais não se disponibiliza para fazer a entrega à instituição e esta, por sua vez, não disponibiliza de um funcionário ou voluntário para o mesmo serviço. 
</p>

                        </Col>
                      </Row>
                    </Container>
                  </div>

                </Col>
              </Row>
            </div>
            <div className="section" id={'noseoutros'}>
              <Container>
              <Row>
                <Col>
                <h3>Como complementamos as associações solidárias já existentes?</h3>
                  <p>
                  Aos doadores e voluntários, é permitida a doação de qualquer tipo de objeto ou intervalo temporal, em qualquer altura do dia ou do ano, e sem compromisso. É também apresentada uma lista das instituições carenciadas mais próximas e sugerida a instituição de entrega mais adequada de acordo com as suas necessidades especificas. 
                  </p>
                  <p>
                  Aos recetores, nomeadamente instituições carenciadas, é possibilitada a receção de uma maior diversidade de artigos e de forma mais alargada em termos temporais.
                  </p>
                </Col>
              </Row>
              </Container>
            </div>

          </Container>
        </div>
        <div className="text-center">
          <Container>
          <iframe width="560" height="315" src="https://www.youtube.com/embed/stmJjtYINu4" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
            <h3>De uma forma ou de outra, todos podemos ajudar!</h3>
            <h1>iGive, do you?</h1>
          </Container>
        </div>
        <DefaultFooter />
      </div>
    </>
  );
}

export default LandingPage;
